
/* ==================== common styles ======================= */
*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
ul {
  list-style: none;
}
body {
  min-width: 320px;
  padding: 3% 0;
  overflow-x: hidden;
  color: #666;
  font-family: RobotoMedium, sans-serif;
  font-size: 12px;
  background: #f0f0f0;
}
input,
input:focus,
img {
  outline: none;
}
.content {
  width: 96%;
  max-width: 660px;
  margin: 0 auto;
  background: url(../img/bg.jpg) repeat;
}
.block {
  padding-left: 20px;
  padding-right: 20px;
  border-bottom: 2px solid #f7f6f4;
  &:last-child {
    border-bottom: none;
  }
}
/*------------------------------ data-block ------------------------------*/
.data-block {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  position: relative;
  padding-top: 30px;
  padding-bottom: 30px;
}
.photo-container {
  flex: 0 0 138px;
  margin: 0 15px 20px 0;
  img {
    width: 100%;
    border-radius: 50%;
    filter: sepia(50%);
  }
}
.info-container {
  flex: 1 1 300px;
  max-width: 460px;
}
.info-item {
  margin-bottom: 34px;
  border-bottom: 2px solid #323232;
  white-space: nowrap;
  &:last-of-type {
    margin-bottom: 0;
  }
}
.param,
.value {
  margin-bottom: 6px;
}
.param {
  display: inline-block;
  width: 160px;
  margin-right: 10px;
  height: 18px;
  text-transform: uppercase;
  letter-spacing: 1.2px;
  font-family: RobotoMedium, sans-serif;
}
.value {
  display: inline-block;
  font-family: Trixie, sans-serif;
  font-size: 14px;
  vertical-align: bottom;
  white-space: normal;
}
.stamp {
  position: absolute;
  bottom: 12px;
  right: 3px;
  width: 184px;
}
/*------------------------------ skills ------------------------------*/
.skills-block {
  padding-top: 34px;
  padding-bottom: 16px;
  h2 {
    margin-bottom: 2px;
  }
}

h2 {
  font-family: RobotoMedium, sans-serif;
  font-size: 18px;
  color: #010101;
}
.skills-list {
  display: flex;
  flex-flow: column wrap;
  align-content: space-between;
  max-height: 200px;
  li {
    width: 140px;
  }
}
input[type="checkbox"] {
  display: none;
}
label {
  height: 60px;
  line-height: 60px;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  cursor: pointer;
  &:before {
    content: "";
    display: inline-block;
    width: 19px;
    height: 19px;
    margin-right: 7px;
    vertical-align: middle;
    background-color: #e5e4e3;
    background-position: 50%;
    background-repeat: no-repeat;
    border-radius: 4px;
    transition: all .8s;
  }
}
input[type="checkbox"]:checked + label:before {
  background-color: #0893d5; /*for smooth change*/
  background-image: url(../img/checked.png);
}
/*------------------------------ scale-block ------------------------------*/
.scale-block {
  display: flex;
  flex-wrap: wrap;
  padding-top: 57px;
  padding-bottom: 15px;
}
.dial-container {
  position: relative;
  width: 254px;
  height: 128px;
  margin-bottom: 15px;
  text-align: center;
  &:after { /*using for "hand" vertical aligning*/
    content: "";
    display: inline-block;
    height: 100%;
    width: 0px;
  }
}
.dial {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.hand-container {
  display: inline-block;
  position: relative;
  bottom: 0;
  width: 64px;
  height: 66px;
  transform: rotate(-90deg);
  transform-origin: bottom center;
  transition: all 2s cubic-bezier(.19,1.9,1,1.9);
}
.hand {
  transform-origin: bottom center;
  animation: tremble 5s infinite;
}
.count-container {
  padding: 9px 0 0 67px;
  margin-bottom: 15px;
  h2 {
    max-width: 220px;
    line-height: 1.3;
    margin-bottom: 13px;
  }
}
.counter {
  font-family: RobotoBlack, sans-serif;
  font-weight: 900;
  font-size: 50px;
  color: #0893d5;
  word-spacing: -10px;
}