
/* ==================== css-animations ======================= */

@keyframes tremble {
	0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(-1deg);
  }
  20% {
    transform: rotate(-1deg);
  }
  30% {
    transform: rotate(1.5deg);
  }
  40% {
    transform: rotate(-1.5deg);
  }
  50% {
    transform: rotate(1deg);
  }
  60% {
    transform: rotate(-1deg);
  }
  70% {
    transform: rotate(0.5deg);
  }
  80% {
    transform: rotate(0deg);
  }
  80% {
    transform: rotate(-0.5deg);
  }
	100% {
    transform: rotate(1deg);
  }
}

@keyframes set {
  0% {
    transform: scale(2);
    opacity: 0;
  }
  
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
